// import { useAnimate, motion } from 'framer-motion';

import {
  motion,
  useAnimate,
  useAnimationFrame,
  useMotionValue,
  useScroll,
  useSpring,
  useTransform,
  useVelocity,
  wrap,
} from 'framer-motion';
import { useRef } from 'react';
import SplitText from '../SplitText';
const Hero = () => {
  const [scope, animate] = useAnimate();

  const baseVelocity = -10;
  const baseX = useMotionValue(0);
  const { scrollY } = useScroll();
  const scrollVelocity = useVelocity(scrollY);
  const smoothVelocity = useSpring(scrollVelocity, {
    damping: 40,
    stiffness: 300,
  });
  const velocityFactor = useTransform(smoothVelocity, [0, 1000], [0, 5], {
    clamp: false,
  });
  const x = useTransform(baseX, (v) => `${wrap(-0, -50, v)}%`);
  const directionFactor = useRef<number>(1);

  useAnimationFrame((t, delta) => {
    let moveBy = directionFactor.current * baseVelocity * (delta / 1000);
    directionFactor.current = 1;
    moveBy += directionFactor.current * moveBy * velocityFactor.get();
    baseX.set(baseX.get() + moveBy);
  });

  return (
    <section className="mt-auto">
      <motion.div
        className="-mb-5 w-full whitespace-nowrap text-[80px] font-extrabold leading-tight text-white"
        ref={scope}
        style={{ x }}
      >
        <SplitText
          initial={{ y: '110%' }}
          animate="visible"
          variants={{
            visible: (i: number) => ({
              y: 0,
              transition: {
                delay: i * 0.1,
                duration: 0.5,
              },
            }),
          }}
        >
          <span>Born&nbsp;</span>
          <span>for&nbsp;</span>
          <span>big&nbsp;</span>
          <span>ideas&nbsp;&nbsp;</span>

          <span>Born&nbsp;</span>
          <span>for&nbsp;</span>
          <span>big&nbsp;</span>
          <span>ideas&nbsp;&nbsp;</span>

          <span>Born&nbsp;</span>
          <span>for&nbsp;</span>
          <span>big&nbsp;</span>
          <span>ideas&nbsp;&nbsp;</span>

          <span>Born&nbsp;</span>
          <span>for&nbsp;</span>
          <span>big&nbsp;</span>
          <span>ideas&nbsp;&nbsp;</span>
        </SplitText>
      </motion.div>
    </section>
  );
};

export default Hero;
